import Axios from 'axios';

const openai = Axios.create({
    baseURL: 'https://api.openai.com/v1',
    headers: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
    },
});

async function RequestCompletion(apiKey: string, query: string) {
    const response = await openai.post('/completions', {
        model: 'text-davinci-003',
        prompt: query,
        max_tokens: 3000,
        temperature: 1,
        n: 1,
    }, {
        headers: {
            'Authorization': ('Bearer ' + apiKey),
        },
    });

    let responseMessage = 'Sorry, Something happened with the request and couldn\'t get a successful response.';
    if (response.status === 200) responseMessage = response.data.choices[0].text ?? 'Sorry. Couldn\'t get a response about that.';

    return responseMessage;
}

export default RequestCompletion;