import { useState } from "react";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";

type AuthBlockerProps = {
    label: string,
    onSetAuthValue: (value: string) => void,
    children: JSX.Element,
};

const AuthBlocker: React.FC<AuthBlockerProps> = (props) => {
    const [inputValue, setInputValue] = useState('');
    const [isValid, setIsValid] = useState<boolean>();
    const [allowed, setAllowed] = useState(false);

    const onKeySubmit = () => {
        if (inputValue.trim().length > 0) {
            setIsValid(true);
            props.onSetAuthValue(inputValue);
            setAllowed(true);
        } else setIsValid(false);
    };

    const onEnterPress = (k: any) => {
        // console.log('Key pressed:', k.keyCode);
        if (k.keyCode === 13) onKeySubmit();
    };

    return (
        allowed ? props.children :
            <Container sx={{
                    height: '100%',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}
            >
                <Box sx={{
                        height: '20%',
                        width: '35%',
                        padding: 2,
                        backgroundColor: '#393E46',
                        border: 1,
                        borderRadius: 5,
                        borderColor: '#B2B2B2',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}
                >
                    <Stack sx={{
                            width: '100%',
                        }}
                    >
                        <TextField autoFocus
                            fullWidth
                            required
                            label={props.label}
                            value={inputValue}
                            onChange={v => setInputValue(v.target.value)}
                            onKeyUp={onEnterPress}
                            error={isValid !== undefined ? !isValid : false}
                            sx={{
                                marginBottom: 2,
                            }}
                        />
                        <Button variant='outlined'
                            color='warning'
                            type='submit'
                            onClick={onKeySubmit}
                        >
                            SUBMIT
                        </Button>
                    </Stack>
                </Box>
            </Container>
    );
};

export default AuthBlocker;