import ChatTextBar from "./ChatTextBar/ChatTextBar";
import ChatOptionBox from "./ChatOptionBox";

import { SelectionQuestion } from "./ChatOptionBox";

type InsertQuestion = {
    id: Number,
    question: string,
    required: boolean,
    answer: string[]|null,
};

type Question = SelectionQuestion|InsertQuestion;

function isSelection(question: SelectionQuestion|InsertQuestion): question is SelectionQuestion {
    const t_q = (question as SelectionQuestion);
    return t_q.options !== undefined && t_q.allowCustomAnswer !== undefined && t_q.customAnswer !== undefined;
}

type QuestionaireChatBarProps = {
    onSend: (value: Question) => void,
    onChange: (value: Question) => void,
    onRequest: boolean,
    question: Question,
};

const QuestionaireChatBar: React.FC<QuestionaireChatBarProps> = (props) => {
    const handleSendInsert = (value: string) => {
        const newQuestion = {...props.question, answer: [value]};
        props.onSend(newQuestion);
    };

    const handleChangeInsert = (value: string) => {
        const newQuestion = {...props.question, answer: [value]};
        props.onChange(newQuestion);
    };

    return isSelection(props.question) ? (
        <ChatOptionBox
            question={props.question}
            onSend={props.onSend}
            onChange={props.onChange}
        />
    ) : (
        <ChatTextBar
            onSend={handleSendInsert}
            onChange={handleChangeInsert}
            onRequest={props.onRequest}
        />
    );
};

export default QuestionaireChatBar;
export type { Question, SelectionQuestion, InsertQuestion };