import { useState, useEffect } from 'react';
import { Box, Checkbox, FormControl, FormControlLabel, FormGroup, FormHelperText, FormLabel, Input, Stack } from "@mui/material"
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';

type SelectionQuestion = {
    id: Number,
    question: string,
    required: boolean,
    answer: string[]|null,
    options: string[],
    allowMultiple: boolean,
    allowCustomAnswer: boolean,
    customAnswer: string|null,
};

type State = {
    checked: {
        [key: string]: boolean,
    },
    selection: string[],
    error: boolean,
    custom: string,
};

type ChatOptionBoxProps = {
    question: SelectionQuestion,
    onSend: (value: SelectionQuestion) => void,
    onChange: (value: SelectionQuestion) => void,
};

const ChatOptionBox: React.FC<ChatOptionBoxProps> = (props) => {
    const [state, setState] = useState<State>({
        checked: {
            none: true,
        },
        selection: [],
        error: false,
        custom: '',
    });

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = {...state}

        if (event.target.name === 'none') {
            newValue.checked = {none: true};
            newValue.selection = [];
        } else if (event.target.name === 'custom')
            newValue.custom = event.target.value.trimStart();
        else {
            // Disable none selection
            if (newValue.checked.hasOwnProperty('none')) newValue.checked.none = false;
            if (newValue.selection.includes('none')) newValue.selection.splice(newValue.selection.indexOf('none'));

            if (event.target.name !== 'other') {
                if (newValue.selection.includes(event.target.name)) newValue.selection.splice(newValue.selection.indexOf(event.target.name), 1);
                else if (props.question.allowMultiple) newValue.selection.push(event.target.name); // add to array if multiple selection is allowed
                else newValue.selection = [event.target.name]; // set array if multiple selection is not allowed
            } else if (!props.question.allowMultiple) newValue.selection = [];

            if (newValue.checked.hasOwnProperty(event.target.name)) newValue.checked[event.target.name] = !newValue.checked[event.target.name];
            else if (props.question.allowMultiple) newValue.checked[event.target.name] = true; // add checked if multiple selection is allowed
            else { // set checked to only one if multiple selection is not allowed
                Object.keys(newValue.checked).forEach(key => newValue.checked[key] = false);
                newValue.checked[event.target.name] = true;
            }
        }

        // set none as checked if no other option is checked
        if (Object.values(newValue.checked).filter(item => item === true).length === 0) {
            newValue.checked = {none: true};
            newValue.selection = [];
        }

        setState(newValue);
        props.onChange({...props.question, answer: state.selection, customAnswer: state.custom});
    };

    const onSubmit = () => {
        const newQuestion = {...props.question, answer: state.selection};
        if (newQuestion.allowCustomAnswer && state.custom.trim().length > 0) newQuestion.customAnswer = state.custom;

        if (newQuestion.required && (newQuestion.answer.length === 0 && (newQuestion.allowCustomAnswer && newQuestion.customAnswer === null))) {
            setState({...state, error: true});
            return;
        }
        
        props.onSend(newQuestion);
        setState({
            checked: {
                none: true,
            },
            selection: [],
            error: false,
            custom: '',
        });
    };

    useEffect(() => {

    }, []);

    return (
        <Box sx={{
                display: 'flex',
                width: '100%',
                marginY: 1,
                padding: 2,
                border: 1,
                borderRadius: 5,
                borderColor: '#B2B2B2',
            }}
        >
            <Stack sx={{ width: '100%' }}>
                <FormControl required={props.question.required}
                    error={state.error}
                    component='fieldset'
                    variant='standard'
                >
                    <FormLabel component='legend' sx={{ fontSize: '0.8rem' }}>
                        Select input
                    </FormLabel>
                    <FormGroup row={true}>
                        {props.question.options.map((option, index) => (
                            <FormControlLabel key={index}
                                label={option.charAt(0).toUpperCase() + option.substring(1).toLowerCase()}
                                control={
                                    <Checkbox size='small'
                                        onChange={handleChange}
                                        name={option}
                                        checked={typeof state.checked[option] !== 'undefined' ? state.checked[option] : false}
                                    />
                                }
                                sx={{
                                    '& .MuiFormControlLabel-label' : {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                        ))}
                        {props.question.allowCustomAnswer && (
                            <FormControlLabel label='Other'
                                control={
                                    <Checkbox size='small'
                                        onChange={handleChange}
                                        name='other'
                                        checked={typeof state.checked.other !== 'undefined' ? state.checked.other : false}
                                    />
                                }
                                sx={{
                                    '& .MuiFormControlLabel-label' : {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                        )}
                        {!props.question.required && (
                            <FormControlLabel label='None'
                                control={
                                    <Checkbox size='small'
                                        onChange={handleChange}
                                        name='none'
                                        checked={state.checked.none}
                                    />
                                }
                                sx={{
                                    '& .MuiFormControlLabel-label' : {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                        )}
                    </FormGroup>
                    <FormHelperText sx={{ fontSize: '0.6rem' }}>
                        Select one
                    </FormHelperText>
                </FormControl>
                {props.question.allowCustomAnswer && (
                    <FormControl fullWidth
                        variant='standard'
                        required={(props.question.required && (typeof state.checked.other !== 'undefined' ? state.checked.other : false))}
                        error={state.error}
                    >
                        <Input disabled={!(typeof state.checked.other !== 'undefined' ? state.checked.other : false)}
                            type='text'
                            name='custom'
                            placeholder='Write custom option'
                            size='small'
                            onChange={handleChange}
                            value={state.custom}
                        />
                    </FormControl>
                )}
            </Stack>
            <Button size='small'
                variant='outlined'
                name='submit'
                sx={{ marginLeft: 2 }}
                endIcon={<SendIcon />}
                onClick={onSubmit}
            >
                SEND
            </Button>
        </Box>
    )
};

export default ChatOptionBox;
export type { SelectionQuestion };