import { useState, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Container from '@mui/material/Container';

import ChatMessageBox from './ChatMessageBox/ChatMessageBox';
import { Message } from './ChatBubble/ChatBubble';
import ChatTextBar from './ChatTextBar/ChatTextBar';

import { useAppSelector, useAppDispatch } from '../app/hooks';
import { addNewMessage, selectMessages } from '../features/messages/messagesSlice';

import RequestCompletion from '../utils/openai';

const welcomeMessage: Message = {
    type: 'announce',
    content: 'Write something in the text box to start.',
    timeStamp: new Date().toLocaleString(),
};

type ChatProps = {
    apiKey: string,
};

const Chat: React.FC<ChatProps> = (props) => {
    const dispatch = useAppDispatch();
    const messageHistory = useAppSelector(selectMessages);

    const [messages, setMessages] = useState<Message[]>([]);
    const [onRequest, setOnRequest] = useState(false);
    const [question, setQuestion] = useState('');

    const sendQuestion = () => {
        if (question.trim().length > 0) {
            const newMessage: Message = {
                type: 'request',
                content: question.trim(),
                timeStamp: new Date().toLocaleString(),
            }
            // dispatch(addNewMessage(newMessage));
            setMessages([...messages, newMessage]);
            console.log('Question sent:', question);
            setOnRequest(true);
        } else setQuestion('');
    };

    const setPreviousMessage = () => {
        const requests = messages.filter(value => value.type === 'request').reverse();
        if (requests.length > 0) setQuestion(requests[0].content);
    };

    useEffect(() => {
        if (messages.length === 0) setMessages([...messageHistory, welcomeMessage]);

        if (onRequest) {
            const prompt = question;
            setQuestion('');

            const startTime = Date.now();
            RequestCompletion(props.apiKey, prompt)
                .then(response => {
                    console.log("OpenAI response:\n\n", response);
                    const newMessage: Message = {
                        type: 'response',
                        content: response,
                        timeStamp: new Date().toLocaleString(),
                        timeTaken: Date.now() - startTime,
                    };
                    // dispatch(addNewMessage(newMessage));
                    setMessages([...messages, newMessage]);
                })
                .finally(() => setOnRequest(false));
        }
    }, [onRequest]);

    return (
        <Container sx={{
                height: '100%',
                width: '65%',
                backgroundColor: '#393E46',
                border: 1,
                borderRadius: 5,
                borderColor: '#B2B2B2',
            }}
        >
            <Stack alignItems='center'
                justifyContent='space-between'
                sx={{
                    height: '100%',
                    paddingX: 2,
                }}
            >
                <ChatMessageBox messages={messages} />
                <ChatTextBar onSend={sendQuestion}
                    onSetPrevMessage={setPreviousMessage}
                    onChange={setQuestion}
                    onRequest={onRequest}
                />
            </Stack>
        </Container>
    );
};

export default Chat;