import { useState, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Container from '@mui/material/Container';

import ChatMessageBox from './ChatMessageBox/ChatMessageBox';
import { MessageType, Message } from './ChatBubble/ChatBubble';
import QuestionaireChatBar, { Question, SelectionQuestion } from './QuestionaireChatBar';

import { useAppDispatch, useAppSelector } from '../app/hooks';
import { addMessages, selectMessages } from '../features/messages/messagesSlice';

const welcomeMessage: Message = {
    type: 'announce',
    content: 'Welcome! Please answer the following questions.',
    timeStamp: new Date().toLocaleString(),
};

type Questionaire = Question[];

type ChatProps = {
    questionaire: Questionaire,
    onFinish: () => void,
};

const CustomQuestionaire: React.FC<ChatProps> = (props) => {
    const dispatch = useAppDispatch();
    const messageHistory = useAppSelector(selectMessages);

    const [messages, setMessages] = useState<Message[]>([...messageHistory, welcomeMessage]);
    const [questionaire, setQuestionaire] = useState(props.questionaire);
    const [chatBar, setChatBar] = useState(<></>);
    const [isFinished, setIsFinished] = useState(false);

    const addMessage = (message: string, type: MessageType) => {
        const newMessage = {
            type: type,
            content: message.trim(),
            timeStamp: new Date().toLocaleString(),
        };
        setMessages(prevMessages => [...prevMessages, newMessage]);
    }

    const updateQuestion = (value: Question) => {
        const newQuestionaire = [...questionaire];
        const index = newQuestionaire.findIndex(question => question.id === value.id);

        if (index >= 0) {
            newQuestionaire[index] = value;
            console.log('Updated questionaire:', newQuestionaire);
        } else console.log('Could not update questionaire');

        setQuestionaire(newQuestionaire);
        const t_value = value as SelectionQuestion;
        addMessage((value.answer !== null && value.answer.length > 0 ? (value.answer.join(', ') + (t_value.allowCustomAnswer && t_value.customAnswer ? ', ' + t_value.customAnswer?.toString() : '')) : 'Empty answer'), 'request');
    };

    useEffect(() => {
        const question = questionaire.find(question => question.answer === null);
        if (question !== undefined) {
            console.log(question.id);
            addMessage(question.question, 'response')
            setChatBar(<QuestionaireChatBar
                onSend={updateQuestion}
                onChange={value => console.log(value.answer)}
                onRequest={false}
                question={question}
            />);
        } else {
            console.log('No more questions to answer');
            setChatBar(<></>);
            addMessage('Thank you for answer, here is the result:\n\n' + JSON.stringify(questionaire), 'response');
            dispatch(addMessages(messages));
            setIsFinished(true);
        }

        if (isFinished) props.onFinish();
    }, [questionaire, isFinished]);

    return (
        <Container sx={{
                height: '100%',
                width: '65%',
                backgroundColor: '#393E46',
                border: 1,
                borderRadius: 5,
                borderColor: '#B2B2B2',
            }}
        >
            <Stack alignItems='center'
                justifyContent='space-between'
                sx={{
                    height: '100%',
                    paddingX: 2,
                }}
            >
                <ChatMessageBox messages={messages} />
                {chatBar}
            </Stack>
        </Container>
    );
};

export default CustomQuestionaire;
export type { Questionaire };