import { useState } from 'react';

import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import CircularProgress from '@mui/material/CircularProgress';
import IconButton from '@mui/material/IconButton';
import SendOutlinedIcon from '@mui/icons-material/SendOutlined';

import './ChatTextBar.css';

type ChatTextBarProps = {
    onSend: (value: string) => void,
    onSetPrevMessage?: () => void,
    onChange: (value: string) => void,
    onRequest: boolean,
};

const ChatTextBar: React.FC<ChatTextBarProps> = (props) => {
    const [value, setValue] = useState('');

    const handleSend = () => {
        props.onSend(value);
        setValue('');
    };

    const handleChange = (input: string) => {
        props.onChange(input);
        setValue(input);
    };

    const onEnterPress = (k: any) => {
        // console.log('Key pressed:', k.keyCode);
        if (k.keyCode === 13) handleSend();
        else if (k.keyCode === 38 && props.onSetPrevMessage !== undefined) props.onSetPrevMessage();
    };

    return (
        <Box sx={{
                width: '100%',
                paddingY: 1,
            }}
        >
            <FormControl fullWidth>
                <OutlinedInput autoFocus
                    fullWidth
                    placeholder='Ask something...'
                    endAdornment={
                        <Box sx={{
                                display: 'flex',
                                flexDirection: 'row',
                                alignItems: 'center',
                            }}
                        >
                            <Box sx={{
                                    marginX: 1,
                                    padding: 0,
                                    display: 'flex',
                                    flexDirection: 'column',
                                }}
                            >
                                <p className='input-counter'>{value.length}</p>
                                <p className='input-counter'>{value.length > 0 ? value.trim().split(' ').length : 0}</p>
                            </Box>
                            {
                                props.onRequest ?
                                    <CircularProgress size='1.5rem' />
                                :
                                    <IconButton aria-label='Send' onClick={handleSend}>
                                        <SendOutlinedIcon />
                                    </IconButton>
                            }
                        </Box>
                    }
                    disabled={props.onRequest}
                    value={value}
                    onChange={q => handleChange(q.target.value)}
                    onKeyUp={onEnterPress}
                    size='small'
                    sx={{
                        // paddingX: 3,
                        // paddingY: 1,
                        // fontSize: '1.5rem',
                        backgroundColor: '#697382',
                        borderRadius: 10,
                        '& .MuiOutlinedInput-notchedOutline': {
                            border: 1,
                            borderColor: '#00ADB5',
                        },
                    }}
                />
            </FormControl>
        </Box>
    );
};

export default ChatTextBar;