import { useState } from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import AuthBlocker from './components/AuthBlocker';
import Chat from './components/Chat';
import CustomQuestionaire from './components/CustomQuestionaire';

import customQuestionaire from './extras/questionaire';

function App() {
  const [apiKey, setApiKey] = useState('');
  const [onQuestionaire, setOnQuestionaire] = useState(true);

  const theme = createTheme({
    palette: { mode: 'dark' }
  });

  const handleQuestionaireFinish = () => {
    setOnQuestionaire(false);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{
          height: '100vh',
          backgroundColor: '#222831'
        }}
      >
        <AuthBlocker label='OpenAI ApiKey' onSetAuthValue={setApiKey}>
          {onQuestionaire ? (
            <CustomQuestionaire questionaire={customQuestionaire} onFinish={handleQuestionaireFinish} />
          ) : (
            <Chat apiKey={apiKey} />
          )}
        </AuthBlocker>
      </Box>
    </ThemeProvider>
  );
}

export default App;
